import React from 'react';
import { makeStyles, createStyles, Theme, useTheme } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import { ShortlineDivider } from './ShortlineDivider';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        section: {
            margin: `${theme.spacing(4)}px auto`,
            padding: `${theme.spacing(3)}px`,
            textAlign: 'center',
            maxWidth: 600,
        },
        actionContainer: {
            display: 'flex',
            justifyContent: 'center',
            marginTop: theme.spacing(1),
            [theme.breakpoints.down('xs')]: {
                flexDirection: 'column',
                alignItems: 'center',
            },
        },
    })
);

type ContentSectionProps = {
    title: string;
    body: string | JSX.Element;
    actions?: JSX.Element[];
};

export const ContentSection = (props: ContentSectionProps): JSX.Element => {
    const { title, body, actions } = props;
    const theme = useTheme();
    const classes = useStyles(theme);

    return (
        <div style={{ backgroundColor: theme.palette.background.default }}>
            <div className={classes.section}>
                <Typography variant={'h4'} style={{ marginBottom: theme.spacing(1) }}>
                    {title}
                </Typography>
                <ShortlineDivider />
                <Typography>{body}</Typography>
                {actions && actions.length > 0 && <div className={classes.actionContainer}>{actions}</div>}
            </div>
        </div>
    );
};
